.cursor {
  z-index: 1000;
  pointer-events: none;
  color: #fff;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.cursor-wrap {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.cursor-inner {
  opacity: 0;
  -o-transition: all .4s ease;
  transform-origin: center;
  background-color: #212121;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 10rem;
  height: 10rem;
  transition: all .4s;
  display: flex;
  position: absolute;
  transform: scale(.2);
}

.cursor-inner.on-hover-drag {
  opacity: 1;
  transform: scale(1);
}

.cursor-ic {
  display: block;
  position: absolute;
}

.cursor-ic.left {
  left: 1.1rem;
}

.cursor-ic.right {
  right: 1.1rem;
  transform: rotate(180deg);
}

